@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{
  body{
    @apply font-light;
  }
  li {
    @apply px-4;
    @apply cursor-pointer;
  }
}

@layer components{
  p.pagetitle{
    @apply text-2xl inline border-b-4 border-[#979795];
  }
  div.skillbox{
    @apply shadow-md shadow-[#c3bdb3] hover:scale-110 duration-500;
  }
}

.content-div{
  background-repeat: none;
  background-size: cover;
  background-position: center;
  height: 250px;
}

.content-div:hover{
  background-image: linear-gradient(
    to right,
    rgba(227, 227, 213, 0.836),
    hsla(58, 20%, 71%, 0.8)
  )!important;
}

input:focus, textarea:focus{
  border: 2px solid #6a6a68;
  outline: none;
}

input, textarea{
  border: 2px solid #efeeed;
  outline: none;
}
